<template>
  <div class="login">
    <el-form ref="form" status-icon :rules="rules" :model="form" label-width="80px">
      <img src="../assets/ps.jpg" alt="">
      <!-- 用户名 -->
      <el-form-item prop='username' label="用户名">
        <el-input @keyup.enter.native="login" v-model="form.username"></el-input>
      </el-form-item>
      <!-- 密码 -->
      <el-form-item  prop='password' label="密码">
        <el-input @keyup.enter.native="login" type='password' v-model="form.password"></el-input>
      </el-form-item>

      <el-form-item>
        <el-button class="loginBtn" type="primary" @keyup.enter="Elogin" @click="login">登陆</el-button>
        <el-button @click="resetForm('form')">重置</el-button>
      </el-form-item>
    </el-form>
  </div>
</template>
<script>
export default {
  data () {
    return {
      form: {
        username: '',
        password: ''
      },
      // 表单校验
      rules: {
        username: [
          { required: true, message: '请输入用户名', trigger: ['blur', 'change'] },
          { min: 4, max: 12, message: '长度在 4 到 12 个字符', trigger: ['blur', 'change'] }
        ],
        password: [
          { required: true, message: '请输入密码', trigger: ['blur', 'change'] },
          { min: 6, max: 12, message: '长度在 6 到 12 个字符', trigger: ['blur', 'change'] }
        ]
      }
    }
  },
  methods: {
    // 登陆
    async login () {
      const { meta, data } = await this.$axios.post('login', this.form)
      if (meta.status === 200) {
        this.$message({
          type: 'success',
          message: meta.msg,
          duration: 1000
        })
        localStorage.setItem('token', data.token)
        this.$router.push('/welcome')
      } else {
        this.$message({
          type: 'error',
          message: meta.msg,
          duration: 1000
        })
      }
    },
    // 重置
    resetForm (form) {
      this.$refs.form.resetFields()
    }
  }
}
</script>

<style lang="scss" >
.login {
  width: 100%;
  height: 100%;
  background-color: #2d434c;
  overflow: hidden;
  .el-form{
    width: 400px;
    padding: 20px;
    padding-top: 80px;
    border-radius: 20px;
    margin: 0 auto;
    margin-top: 200px;
    background-color: #fff;
    position: relative;
    .loginBtn{
      margin-right: 80px;
    }
    img{
      width: 100px;
      height: 100px;
      position: absolute;
      left: 50%;
      transform: translateX(-50%);
      top: -50px;
      border-radius: 50%;
      border: 3px solid #ccc;
      box-shadow: 0 0 15px #ccc
    }
  }
}

</style>
