<template>
  <el-container class="index">
    <el-header>
      <div class="logo">
        <!-- ../assets/logo.ec55f206.png -->
        LOGO
        <img src="" alt="">
      </div>
      <div class="title">
        <h1>电商后台管理系统</h1>
      </div>
      <div class="useOut">
        <span>欢迎光临~ </span>
        <a href="javascript:;" @click="useOut">退出</a>
      </div>
    </el-header>
    <el-container>
      <el-aside width="200px">
        <!-- 导航 -->
        <el-menu
          router
          unique-opened
          :default-active="defaultActive"
          class="el-menu-vertical-demo"
          background-color="#545c64"
          text-color="#fff"
          active-text-color="#ffd04b">

          <el-submenu :index="menu.path" v-for="menu in data" :key="menu.id">
            <template v-slot:title>
              <i class="el-icon-location"></i>
              <span>{{menu.authName}}</span>
            </template>
            <el-menu-item :index="item.path" v-for="item in menu.children" :key="item.id">
              <i class="el-icon-menu"></i>
              <span slot="title">{{item.authName}}</span>
            </el-menu-item>
          </el-submenu>

        </el-menu>
      </el-aside>
      <el-main>
        <!-- 欢迎来到后台管理系统 -->
        <router-view></router-view>
      </el-main>
    </el-container>
  </el-container>
</template>

<script>
export default {
  data () {
    return {
      data: []
    }
  },
  async created () {
    const { data, meta } = await this.$axios.get('menus')
    if (meta.status === 200) {
      this.data = data
    }
  },
  methods: {

    useOut () {
      this.$confirm('呔！尔当真要退出？', '温馨提示', {
        type: 'warning'
      }).then(() => {
        this.$message.success('恭喜，退出成功')
        localStorage.removeItem('token')
        this.$router.push('/login')
      }).catch(() => {
        this.$message('取消成功')
      })
    }
  },
  computed: {
    defaultActive () {
      // console.log(this.$route)
      return this.$route.path.slice(1).split('-')[0]
    }
  }
}
</script>

<style lang="scss" >
  .index{
    height: 100%;
    .el-header{
      background-color: #d8d8d8;
      display: flex;
      .logo{
        width: 180px;
        padding: 10px;
        img{
          height: 40px;
        }
      }
      .title{
        overflow: hidden;
        flex: 1;
        line-height: 60px;
        text-align: center;
      }
      .useOut{
        width: 180px;
        font-weight: 700;
        line-height: 60px;
        text-align: right;
        a{
          color: orange;
        }
      }
    }
    .el-aside{
      background: #545c64;
      .el-menu{
        border: none;
      }
    }
    .el-main{
      background: #ecf0f1;
    }
  }
</style>
